table {
  width: 100%;
  height: 60vh;
  max-width: 1024px;
  border: 1px solid #bbb;
  border-radius: 20px;
  border-collapse: collapse;
  text-align: center;
  margin: 2vh auto;
  table-layout: fixed;
}

tr {
  width: 100%;
}

th, td {
  border: 1px solid #bbb;
  overflow: hidden;
}

.first_col {
  width: 20%;
}

.checked {
  background-color: green;
}

.no_work {
  background-color: rgb(165, 1, 1);
}

.checking {
  background-color: rgb(255, 93, 93);
}

.in_progress {
  background-color: rgb(242, 255, 57);
}

.todo {
  background-color: rgb(170, 170, 170);
}
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app{
  width: 70%;
  height: 100%;
  max-width: 1024px;
  margin: 0 auto;
}

header {
  width: 100%;
  height: 20vh; 
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.apology {
  margin: 30vh auto;
  width: 80%;
  text-align: center;
}

.selector {
  flex-basis: 60%;
}

.avatar {
  flex-basis: 20%;
  text-align: center;
}

.avatar img {
  width: 30%;
  justify-self: center;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
