* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app{
  width: 70%;
  height: 100%;
  max-width: 1024px;
  margin: 0 auto;
}

header {
  width: 100%;
  height: 20vh; 
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.apology {
  margin: 30vh auto;
  width: 80%;
  text-align: center;
}

.selector {
  flex-basis: 60%;
}

.avatar {
  flex-basis: 20%;
  text-align: center;
}

.avatar img {
  width: 30%;
  justify-self: center;
}

.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}