table {
  width: 100%;
  height: 60vh;
  max-width: 1024px;
  border: 1px solid #bbb;
  border-radius: 20px;
  border-collapse: collapse;
  text-align: center;
  margin: 2vh auto;
  table-layout: fixed;
}

tr {
  width: 100%;
}

th, td {
  border: 1px solid #bbb;
  overflow: hidden;
}

.first_col {
  width: 20%;
}

.checked {
  background-color: green;
}

.no_work {
  background-color: rgb(165, 1, 1);
}

.checking {
  background-color: rgb(255, 93, 93);
}

.in_progress {
  background-color: rgb(242, 255, 57);
}

.todo {
  background-color: rgb(170, 170, 170);
}